import React ,{useRef,useState} from 'react'
import {Form,Button,Card} from 'react-bootstrap'
import { useAuth } from "../context/authContext"
import {useHistory ,Link} from "react-router-dom"



export default function Dashboard()

{
    const NameRef = useRef()
    const dateRef = useRef()
    const placeRef = useRef()
    const timeRef = useRef()
    const [setError]=useState('')
    const {currentUser, logout} = useAuth()
    const history = useHistory()


    async function handleLogout() 
    {
       
    
        try {
          await logout()
          history.push("/login")
        } catch {
          setError("Failed to log out")
        }
    }

return(
    <>
        <Card>
            <Card.Body>
                <h2 className="text-center nb-4">Your Profile</h2>
                <strong>Email: </strong>{currentUser.email}
                <Form>
                                    <Form.Group id="name">
                        <Form.Label>Enter Your Full Name </Form.Label>
                        <Form.Control type="text" ref={NameRef} required />
                    </Form.Group>
                    <Form.Group id="DOB">
                        <Form.Label>Enter Your Date of Birth </Form.Label>
                        <Form.Control type="date" ref={dateRef} required />
                    </Form.Group>
                    <Form.Group id="POB">
                        <Form.Label>Enter Your Place of Birth </Form.Label>
                        <Form.Control type="Place" ref={placeRef} required />
                    </Form.Group>
                    <Form.Group id="TOB">
                        <Form.Label>Enter Your Time of Birth </Form.Label>
                        <Form.Control type="time" ref={timeRef} required />     
                    </Form.Group>          
                </Form>
           </Card.Body>
        </Card>
        <Button variant="link" className="align-item:center" onClick={handleLogout}>Log Out</Button>
        <Link to="/SecondPage" ClassName="btn btn-primary w-100 mt-3">Save</Link>

    </>      
    )
} 