import React ,{useState} from 'react'
import {Button,Card} from 'react-bootstrap'
import { useAuth } from "../context/authContext"
import {useHistory } from "react-router-dom"



export default function Second()

{
    
    const [setError]=useState('')
    const {currentUser, logout} = useAuth()
    const history = useHistory()

    async function handleLogout() 
    {
       
    
        try {
          await logout()
          history.push("/login")
        } catch {
          setError("Failed to log out")
        }
    }

    return(
    <>
        <Card>
            <Card.Body>
                <h2 className="text-center nb-4">Your Profile</h2>
                <strong>Email: </strong>{currentUser.email}<br/><br/>
                <strong>Name: </strong>{currentUser.Name }<br/><br/>
                <strong>Date Of Birth: </strong>{currentUser.date}<br/><br/>
                <strong>Place Of Birth: </strong>{currentUser.place}<br/><br/>
                <strong>Time Of Birth : </strong>{currentUser.time}<br/><br/>
                        
                
           </Card.Body>
        </Card>
         <div class="dw-100 text-center mt-2">
            <Button variant="link" className="align-item:center" onClick={handleLogout}>Log Out</Button>
           <Button variant="link" className="align-item:center">Edit</Button>
        </div> 
        
    </>      
    )
} 